import React from "react";
import gql from "graphql-tag";
import { useQuery } from "@apollo/react-hooks";
import sprite from "../../assets/images/sprite.svg";
import { useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";



const getBlogPostsData = gql`
    query($slug : String!){
        cuflBlogs(where: {slug : $slug} ){
                    id,
            imageDesc,
            postImage {
            id,
            url
            },
            postExcerpt,
                    postDate,
                    postHeading,
                    slug,
                    postBody{
                        html
                    }

        }
    }

`;

function FullBlogPosts(props) {

    let history = useHistory();
    function backToBlogHandler(){
        history.push("/blog");
    }

    let { slug } = useParams();
    
    const { loading, error, data } = useQuery(getBlogPostsData, { variables : { slug: slug}});

    
  if (loading) return "Loading...";
  if (error) return `Error! ${error.message}`;
    console.log(data)
    console.log(props)
  return (
      
    <div className="blog-posts">
                <div className="container">
                    <div className="blog-posts__container">
                        <div className="blog-posts__card">
                            <div className="blog-posts__card--image">
                                <img className="blog-posts__card--image-img" src={data.cuflBlogs[0].postImage.url} alt={data.cuflBlogs[0].imageDesc}/>
                            </div>
                            <div className="blog-posts__card--text">
                                <h3 className="blog-posts__card--text-heading">{data.cuflBlogs[0].postHeading}</h3>
                                <div className="blog-posts__card--text-excerpt" dangerouslySetInnerHTML={{__html: data.cuflBlogs[0].postBody.html}} />
                                
                            <p className="blog-posts__card--text-date">{data.cuflBlogs[0].postDate}</p>
                                <a href="#" className="blog-posts__card--link">Read More<span><svg className="about__link--icon">
                                    <use xlinkHref={sprite + "#icon-chevron-right"} />
                                </svg></span></a>
                            </div>
                        </div>
                        <button type="button" onClick={backToBlogHandler}> # Back </button>
                    </div>
                </div>
            </div>
      
  );
  
}





export default FullBlogPosts;
