import React from "react";
import { Link } from "react-router-dom";


function footer (props) {

    let today = new Date();
    let thisYear = today.getFullYear();

    return(
        <React.Fragment>
            <footer className={"footer footer-"+ props.page}>
            <div className="container">
                <div className="footer__box">
                    <div className={"fcard " + props.activeCufl}>
                        <Link to="/cufl" className="fcard__heading">CUFL</Link>
                        <ul className="fcard__list">
                            <li><Link to="/cufl" className="fcard__link">About Us</Link></li>
                            <li><Link to="/cufl" className="fcard__link">Competition</Link></li>
                            <li><Link to="/cufl#cufl-rules" className="fcard__link">Rules</Link></li>
                            <li><Link to="/cufl" className="fcard__link">Leagues</Link></li>
                            <li><Link to="/cufl#cufl-committee" className="fcard__link">Committee</Link></li>
                        </ul>
                    </div>
                    <div className={"fcard " + props.activeCfai}>
                        <Link to="/cfai" className="fcard__heading">CFAI</Link>
                        <ul className="fcard__list">
                            <li><Link to="/cfai" className="fcard__link">About Us</Link></li>
                            <li><Link to="/cfai" className="fcard__link">Competition</Link></li>
                            <li><Link to="/cfai" className="fcard__link">Rules</Link></li>
                            <li><Link to="/cfai" className="fcard__link">Committee</Link></li>
                        </ul>
                    </div>
                    <div className={"fcard " + props.activeIufu}>
                        <Link to="/iufu" className="fcard__heading">IUFU</Link>
                        <ul className="fcard__list">
                            <li><Link to="/iufu" className="fcard__link">About Us</Link></li>
                            <li><Link to="/iufu" className="fcard__link">Competition</Link></li>
                            <li><Link to="/iufu" className="fcard__link">Rules</Link></li>
                            <li><Link to="/iufu" className="fcard__link">Committee</Link></li>
                        </ul>
                    </div>
                    <div className={"fcard " + props.activeWscai}>
                        <Link to="/wscai" className="fcard__heading ">WSCAI</Link>
                        <ul className="fcard__list">
                            <li><Link to="/cufl" className="fcard__link">About Us</Link></li>
                            <li><Link to="/wscai" className="fcard__link">Competition</Link></li>
                            <li><Link to="/wscai" className="fcard__link">Rules</Link></li>
                            <li><Link to="/wscai" className="fcard__link">Committee</Link></li>
                        </ul>
                    </div>
                    
                </div>
                <p className="footer__content">
                    COPYRIGHT &copy; <span>{thisYear}</span>, CUFL
                </p>
            </div>
            </footer>
        </React.Fragment>
    );
}


export default footer