import React from "react";
import gql from "graphql-tag";
import { useQuery } from "@apollo/react-hooks";
import sprite from "../../assets/images/sprite.svg";
import { Link } from 'react-router-dom';


const getBlogPostsData = gql`
    query($orderBy: CuflBlogOrderByInput){
        cuflBlogs(orderBy: $orderBy){
   
            id,
            imageDesc,
            postImage {
              id,
              url
            },
            postExcerpt,
            postDate,
            postHeading,
            slug

        }
    }



`;


function Blogs() {
    const { loading, error, data } = useQuery(getBlogPostsData, {variables: {"orderBy":"postDate_DESC"}});

    
  if (loading) return "Loading...";
  if (error) return `Error! ${error.message}`;
    console.log(data)
  return (

    data.cuflBlogs.map(el => 
        <div className="blog-posts">
        <div className="container">
            <div className="blog-posts__container">
            <div className="blog-posts__card" key={el.id}>
            <div className="blog-posts__card--image">
                <img className="blog-posts__card--image-img" src={el.postImage.url} alt={el.imageDesc}/>
            </div>
            <div className="blog-posts__card--text">
                <h3 className="blog-posts__card--text-heading">{el.postHeading}</h3>
                <p className="blog-posts__card--text-excerpt">{el.postExcerpt}</p>
  <p className="blog-posts__card--text-date">{el.postDate}</p>
                <Link to={`/blog/${el.slug}`} className="blog-posts__card--link">Read More<span><svg className="about__link--icon">
                    <use xlinkHref={sprite + "#icon-chevron-right"} />
                </svg></span></Link>
            </div>
        </div>
            </div>
        </div>
    </div>
        
        
        
        )
      
  );
}

export default Blogs;