import React from "react";
import sprite from "../../../../assets/images/sprite.svg";


function CuflNav(props) {


    return(
        <ul className="main-navigation__nav">
            <li className="main-navigation__nav--item disappear"><a href="#cufl-about" className="main-navigation__nav--links" onClick={props.clicked}>About Us</a></li>
            <li className="main-navigation__nav--item disappear"><a href="#cufl-competition" className="main-navigation__nav--links" onClick={props.clicked}>Competition</a></li>
            <li className="main-navigation__nav--item disappear"><a href="#cufl-rules" className="main-navigation__nav--links" onClick={props.clicked}>Rules</a></li>
            <li className="main-navigation__nav--item disappear"><a href="#cufl-leagues" className="main-navigation__nav--links" onClick={props.clicked}>Leagues</a></li>
            <li className="main-navigation__nav--item disappear"><a href="#cufl-committee" className="main-navigation__nav--links" onClick={props.clicked}>Committee</a></li>
   
            <li className="main-navigation__nav--item main-navigation__resources" onClick={props.clicked}>
                <a href="#" className="main-navigation__nav--links">More 
                    <span><svg className="main-navigation__resources--icon">
                    <use xlinkHref={sprite + "#icon-chevron-down"}></use></svg></span>                                            
                </a>
                    <ul className="main-navigation__resources--dropdown">
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#cufl-futsal">Intl. Team</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#cufl-futsal">Futsul</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#">Contacts</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#">Club Guide</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#">Team Sheet</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#cufl-referee">Referees</a></li>
                        <li className="main-navigation__nav--item"><a className="main-navigation__nav--links" href="#cufl-referee">Blog</a></li>
                    </ul>
                
            </li>
        </ul>
    );
}

export default CuflNav


{/*



        transform: [dropDown ? "scaleY(0)" : "scaleY(1)"],*/}