import React, { useState } from "react";
import gql from "graphql-tag";
import { useQuery } from "@apollo/react-hooks";
import Loader from "../Ui/Loader/Loader";

import CuflNav from "../Ui/PageMasthead/CuflNavs/CuflNavs";
import "./Cufl.css";

import Masthead from "../Ui/PageMasthead/PageMasthead";
import Footer from "../Ui/Footer/Footer";

import Emblem from "../Sections/SectionEmblem";
import About from "../Sections/SectionAbout";
import Competiton from "../Sections/SectionCompetition";
import Rules from "../Sections/SectionRules";
import Leagues from "../Sections/SectionLeague/SectionLeagues";
import International from "../Sections/SectionInternational";
import Futsal from "../Sections/SectionFutsal";
import Committee from "../Sections/SectionCommittee/SectionComittee";
import Person from "../Sections/SectionCommittee/CommitteeCard/CommitteeCard";
import Resources from "../Sections/SectionResources/SectionResources";
import Referee from "../Sections/SectionResources/refereeCard/refereeCard";



import Logo from "../../assets/images/indesign.png";

const getCuflPageData = gql`
    query{
        cuflPages{
            cuflAbout,
            cuflAboutImage {
              id,
              url
            },
            cuflAboutImageDesc,
            competitionText,
            competitionForm{
                id,
                url
            },
            rulesImage {
              id,
              url
            },
            rulesImageDesc,
            ruleWhose,
            ruleOne,
            ruleTwo,
            ruleThree,
            ruleFour,
            rulesPdf{
                id,
                url
            },
            internationalTeamText,
            internationalTeamImage {
              id,
              url
            },
            internationalTeamImageDesc,
            cuflEmail,
            cuflTwitter,
            cuflFacebook,
            cuflInstagram,
            clubGuide{
              id,
              url
            },
            teamSheet{
              id,
              url
            },
            rulesPdf{
              id,
              url
            }
          },
        cuflCommittees{
            id,
            personName,
            personRole,
            personEmail,
            personPicture{
                id,
                url
            }
        
        },
        cuflRefereeses{
            id,
            refereeName,
            refereeEmail,
            refereeCounty,
            refereeMobile
        }
    }

`;




function Cufl() {

    const [nav, setNav] = useState(false);

    const { loading, error, data } = useQuery(getCuflPageData, {variables: { "id" : "ck6rpq0f8b3qx0b20rv5shcc6" }});

    

    if (loading) return (
    <Loader />
    );
    console.log(data);

    if (error) return `Error! ${error.message}`;



    return(
        <React.Fragment>
            <Masthead identifier={"cufl-nav"} default={nav} changed={() => setNav(!nav)}>
                <CuflNav clicked={() => setNav(!nav)}/>
            </Masthead>
            <main>
                <div className="main-content">
                    <div className="content-info">
                        <Emblem 
                            identifier="cufl"
                            logo={Logo} 
                            logoAlt="Cufl logo"
                            committee="CUFL"
                            committeeDesc="Colleges and Universities Football League"
                        />
                        <About 
                            identifier="cufl"
                            context={data.cuflPages[0].cuflAbout}
                            image={data.cuflPages[0].cuflAboutImage.url}
                            imageDesc={data.cuflPages[0].cuflAboutImageDesc}
                            path="/"
                        />
                        <Competiton 
                            identifier="cufl"
                            context={data.cuflPages[0].competitionText}
                            competitionPdf={data.cuflPages[0].competitionForm.url}
                            
                        />
                        <Rules 
                            identifier="cufl"
                            ruleImage={data.cuflPages[0].rulesImage.url}
                            ruleImageDesc={data.cuflPages[0].rulesImageDesc}
                            ruleWhose={data.cuflPages[0].ruleWhose}
                            ruleOne={data.cuflPages[0].ruleOne}
                            ruleTwo={data.cuflPages[0].ruleTwo}
                            ruleThree={data.cuflPages[0].ruleThree}
                            ruleFour={data.cuflPages[0].ruleFour}
                            rulePdf={data.cuflPages[0].rulesPdf.url}

                        />
                        <Leagues identifier="cufl" />
                        <International 
                            identifier="cufl"
                            context={data.cuflPages[0].internationalTeamText}
                            Intlimage={data.cuflPages[0].internationalTeamImage.url}
                            IntlImageDesc={data.cuflPages[0].internationalTeamImageDesc}
                        />
                        <Futsal />
                        <Committee identifier="cufl">
                            {
                                data.cuflCommittees.map(el => (
                                    <Person key={el.id} personImage={el.personPicture.url} personEmail={el.personEmail} personRole={el.personRole} personName={el.personName}/>
                                ))
                            }
                        </Committee>
                        <Resources 
                            identifier="cufl" 
                            email={data.cuflPages[0].cuflEmail}
                            facebook={data.cuflPages[0].cuflFacebook}
                            twitter={data.cuflPages[0].cuflTwitter}
                            instagram={data.cuflPages[0].cuflInstagram}
                            clubGuide={data.cuflPages[0].clubGuide.url}
                            teamSheet={data.cuflPages[0].teamSheet.url}
                        >
                            {
                                data.cuflRefereeses.map(el => (
                                    <Referee key={el.id} county={el.refereeCounty} refereeName={el.refereeName} refereeMobile={el.refereeMobile} refereeEmail={el.refereeEmail} identifier="cufl" />
                                ))
                            }
                        </Resources>
                    </div>
                </div>
            </main>
            <Footer 
            page="cufl"
            activeCufl="fcard-active"
            activeIufu=""
            activeCfai=""
            activeWscai=""
            />
        </React.Fragment>
    );
}

export default Cufl;
