import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";





import Landing from "../../pages/Landing/Landing";
import CuflPage from "../../components/Cufl/Cufl";
import CfaiPage from "../../components/Cfai/Cfai";
import IufuPage from "../../components/Iufu/Iufu";
import WscaiPage from "../../components/Wscai/Wscai";
import Blogs from "../../components/Blog/Blogs";
import BlogSingle from "../../components/Blog/Blog-single";


class Layout extends Component {


    render () {


        return(
            <React.Fragment>

                <Route path="/" exact component ={Landing} />
                <Switch>
                    <Route path="/index" exact component ={Landing} />
                    <Route path="/cufl" exact component ={CuflPage} />
                    <Route path="/cfai" exact component ={CfaiPage} />
                    <Route path="/iufu" exact component ={IufuPage} />
                    <Route path="/wscai" exact component ={WscaiPage} />
                    <Route path="/blog" exact component ={Blogs} />
                    <Route path="/blog/:slug" exact component ={BlogSingle} />
                    {/* <Route component ={Landing} />  */}
                </Switch>
            </React.Fragment>
        );
    }
};

export default Layout;